/* eslint-disable */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import styles from './AuthModalContainerStyles';
import * as RegisterPage from '../../pages/auth/RegisterPage';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';
import { getAuthInfoSelector } from '../../reducers/rootReducer';
import { handleRegisterActionCreator } from '../../reducers/auth/authActionCreators';
/* eslint-enable */

class AuthModalContainer extends Component {
    static defaultProps = {
      open: false
    }

    static propTypes = {
      open: PropTypes.bool.isRequired,
      authInfo: PropTypes.object.isRequired
    }

    render() {
      const { open, authInfo, fullScreen } = this.props;
      return (
        <Dialog
          // disableBackdropClick
          // disableEscapeKeyDown
          fullScreen={fullScreen}
          open={authInfo.isRegistering || open}
          onClose={this.handleClose}
          aria-labelledby="responsive-dialog-title"
          color="inherit"
        >
          <DialogTitle id="responsive-dialog-title">
            {authInfo.isRegistering ? RegisterPage.PAGE_NAME : 'Login'}
          </DialogTitle>
          { authInfo.isRegistering && <RegisterPage.Content /> }
          { authInfo.isRegistering && <RegisterPage.Buttons /> }
        </Dialog>
      );
    }
}

AuthModalContainer.propTypes = {
  fullScreen: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired
};

const StyledAuthModalContainer = withStyles(styles)(AuthModalContainer);

const WithDIalogAuthModalContainer = withMobileDialog()(StyledAuthModalContainer);

const mapStateToProps = state => ({
  authInfo: getAuthInfoSelector(state)
});
const mapDispatchToProps = dispatch => ({
  closeAuthenticationWindow: dispatch(handleRegisterActionCreator({}))
});
const ReduxAuthModalContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(WithDIalogAuthModalContainer);

export default ReduxAuthModalContainer;
